export default class AnimationInfoBg {
  constructor() {
    this.parentEl = document.querySelector('.js-sectionInfo');
    this.bgColor = this.parentEl.querySelector('.js-animationColor');
    this.browser = this.parentEl.querySelector('.js-browser');
    this.video = document.querySelector('#videoHeader');
  }

  playing() {
    const checkPoint = this.parentEl.querySelector('.js-firstStep');
    
    return function(scroll){
      const screenHeight = window.innerHeight;
      const top = checkPoint.getBoundingClientRect().top;
      let position = scroll + top - screenHeight * .4;

      if (position < scroll) {
        if (this.video.readyState >= 3) {
          this.video.play();
        }
      } else {
        this.video.pause();
      }
    }.bind(this)
  }

  fading() {
    const checkPoint = this.parentEl.querySelector('.js-secondStep');

    return function(scroll){
      const screenHeight = window.innerHeight;
      const top = checkPoint.getBoundingClientRect().top;
      let position = scroll + top - screenHeight * .4;

      if (position < scroll) {
        setTimeout(function() {
          this.browser.classList.add('active');
        }.bind(this), 300);
      } else {
        this.browser.classList.remove('active');
      }
    }.bind(this)
  }

  coloring() {
    const checkPointFirst = this.parentEl.querySelector('.js-secondStep');
    const checkPointSecond = this.parentEl.querySelector('.js-thirdStep');

    return function(scroll){
      const screenHeight = window.innerHeight;
      const topFirst = checkPointFirst.getBoundingClientRect().top;
      const topSecond = checkPointSecond.getBoundingClientRect().top;
      let positionFirst = scroll + topFirst - screenHeight * .8;
      let positionSecond = scroll + topSecond - screenHeight * .8;

      if (positionFirst < scroll) {
        this.bgColor.classList.add('activeFirst');
      } else {
        this.bgColor.classList.remove('activeFirst');
      }

      if (positionSecond < scroll) {
        this.bgColor.classList.add('activeSecond');
      } else {
        this.bgColor.classList.remove('activeSecond');
      }
    }.bind(this)
  }
}