export default class TextAnimation {
  constructor() {
    this.parentEl = document.querySelector('.js-sectionHeader');
    this.description = this.parentEl.querySelector('.js-description');
    this.descriptionEl = this.parentEl.querySelector('.js-descriptionBox');
  }

  translating() {
    const emptyEl = this.parentEl.querySelector('.sectionHeader__empty');

    return function(scroll) {
      const screenHeight = window.innerHeight;

      const {height} = emptyEl.getBoundingClientRect()

      const startPosition = scroll + this.description.getBoundingClientRect().top - screenHeight * .4;
      const endPosition = startPosition + height;
      let progress = (scroll - startPosition) / (endPosition - startPosition);

      if (scroll < startPosition) {
        this.descriptionEl.style.transform = `translateY(0px)`;
      } else if (scroll < endPosition) {
        this.descriptionEl.style.transform = `translateY(${height * progress}px)`;
      }
    }.bind(this)
  }
}
