export default class ImageBgAnimation {
  constructor() {
    this.infoEl = document.querySelector('.js-sectionInfo');
    this.infoImage = this.infoEl.querySelector('.js-animationImage');
  }

  changingImage() {
    const checkPoint = document.querySelector('.js-sectionMedia');
  
    return function(scroll) {
      const screenHeight = window.innerHeight;
      const top = checkPoint.getBoundingClientRect().top;
      let position = scroll + top - screenHeight * .001;
  
      if (position < scroll) {
        this.infoImage.classList.add('active');
      } else {
        this.infoImage.classList.remove('active');
      }
    }.bind(this)
  }
  
}