import "./main.scss";
import ImageBgAnimation from './templates/changeBgImage';
import TextAnimation from './templates/header/translatingText';
import HeaderAnimations from './templates/header/header';
import InfoAnimations from './templates/info/info';

document.addEventListener( 'DOMContentLoaded', event => {
  const headerAnimations = new HeaderAnimations;
  const headerBluring = headerAnimations.bluring();
  const headerColoring = headerAnimations.coloring();
  const headerPlaying = headerAnimations.playing();
  headerAnimations.setTitleElHeight();

  const textAnimation = new TextAnimation;
  const textTransforming = textAnimation.translating();

  const imageBgAnimation = new ImageBgAnimation;
  const changingBgImage = imageBgAnimation.changingImage();

  const infoAnimations = new InfoAnimations;
  const infoPlaying = infoAnimations.playing();
  const infoFading = infoAnimations.fading();
  const infoColoring = infoAnimations.coloring();

  window.addEventListener('scroll', event => {
    const scroll = window.pageYOffset;

    changingBgImage(scroll);

    headerBluring(scroll);
    headerColoring(scroll);
    headerPlaying(scroll);

    textTransforming(scroll);

    infoPlaying(scroll);
    infoFading(scroll);
    infoColoring(scroll);
  }, {passive: true});
});
